/* Your custom fonts here */
@include font-face('RobotoRegular', '../fonts/Roboto Regular V2', normal, italic, $exts: woff woff2 ttf);
@include font-face('RobotoLight', '../fonts/Roboto Light V2', normal, italic, $exts: woff woff2 ttf);
@include font-face('RobotoBold', '../fonts/Roboto Bold V2', normal, italic, $exts: woff woff2 ttf);
@include font-face('RobotoLightItalic', '../fonts/Roboto Light Italic V2', normal, italic, $exts: woff woff2 ttf);
@include font-face('RobotoMedium', '../fonts/Roboto Medium V1', normal, italic, $exts: woff woff2 ttf);

@include font-face('UbuntuMedium', '../fonts/Ubuntu Medium', normal, italic, $exts: woff woff2 ttf);
@include font-face('UbuntuBold', '../fonts/Ubuntu Bold', normal, italic, $exts: woff woff2 ttf);


$font-1: RobotoRegular, Helvetica, Arial, sans-serif;
$font-1-l: RobotoLight , Helvetica, Arial, sans-serif;
$font-1-m: RobotoMedium, Helvetica, Arial, sans-serif;
$font-1-b: RobotoBold , Helvetica, Arial, sans-serif;
$font-1-l-italic: RobotoLightItalic, Helvetica, Arial, sans-serif;

$font-2: UbuntuMedium, Helvetica, Arial, sans-serif;
$font-2-b: UbuntuBold, Helvetica, Arial, sans-serif;

$h1-m: 30px;
$h2-m: 26px;
$h3-m: 22px;
$h4-m: 20px;
$h5-m: 14px;

$h1-t: 36px;
$h2-t: 28px;
$h3-t: 24px;
$h4-t: 22px;
$h5-t: 20px;

$h1-d: 44px;
$h2-d: 36px;
$h3-d: 30px;
$h4-d: 22px;
$h5-d: 20px;




