.order-p2__wrap {
  padding: 20px;
  padding-top: 0px;
}

.order-p2__ttl {
  font-family: $font-2;
  font-size: $h2-m;
  color: $color-1;
  text-align: center;
  margin: 0;
}

.order-p2__info {
  @include flex;  
  font-family: $font-2;
  background: url(%=static=%content/txt-bg.png)no-repeat 50%;
  background-size: contain;
  padding: 10px;
  text-align: center;
  color: #fff;  
  font-size: 20px;
  margin-top: 10px;
}

.order-p2__link {
  margin-left: 20px;
  margin-right: 20px;
  margin-top: 20px;
  @include btn;
}

.order-p2__ttl-desk {
  display: none;
}


@media #{$tablet} {
  .order-p2__wrap {
    width: $max-tablet;
    margin: auto;
  }
  
  .order-p2__ttl {
    font-size: $h2-t;
    text-transform: uppercase;
    width: 70%;
    margin: auto;
  }
  
  .order-p2__info {
    font-size: $h2-t;
    text-transform: uppercase;
  }
  
  .order-p2__link {
    width: 430px;
    margin: auto;
    margin-top: 30px;
  }
}


@media #{$desktop} {
  .order-p2__wrap {
    width: $max-desktop;   
    margin-top: 35px;      
    @include flex-row;    
  }
  
  .order-p2__ttl {
    display: inline-block;
    width: auto;
    width: 650px;
    padding-left: 140px;
    font-size: $h3-d;
    line-height: 50px;
    text-align: left;
    margin: 0;
  }
  
  .order-p2__info {
    display: none;
  }
  
  .order-p2__ttl-desk {
    display: inline-block;
    background: url(%=static=%content/txt-bg.png)no-repeat 50%;
    background-size: contain;
    padding: 30px;
    color: #fff;
    letter-spacing: -0.3px;
  }
  
  .order-p2__link {
    width: 335px;
    margin-top: 20px;
    padding: 12px;
    margin-left: 40px;
  }
}
