.guarantees {
  background: #373737 url(%=static=%/content/our-guarantees.jpg)no-repeat 35%;
  background-size: cover;
}

.guarantees__wrap {
  padding: 20px;
  margin-top: 30px;
}

.guarantees__ttl {
  color: $color-2;
  font-size: 30px;
  font-family: $font-2-b;
  text-shadow: 1px 1px 1px rgba(0,0,0, 0.7);
  margin-top: 0px;
  padding: 0;
  text-align: right;
}

.guarantees__info {
  font-family: $font-1-l-italic;
  font-size: 14px;
  color: #fff;
  text-shadow: 1px 1px 1px rgba(0,0,0, 1);
  text-align: right;
  margin-top: 35px;
}


@media #{$tablet} {
  .guarantees {
    background-position: 45%;
  }
  .guarantees__wrap {
    width: $max-tablet;    
    padding-top: 60px;
    padding-bottom: 40px;
    margin: auto;      
  }
  
  .guarantees__ttl {
    font-size: $h1-t;    
  }
  
  .guarantees__info {
    font-size: 20px;
  }
}