.ourService__wrap {
  padding: 20px;
}

.ourService__ttl {
  @include line-p;
  font-size: $h2-m; 
}

.ourService__items {
  @include nl;
  @include flex-row;
  flex-wrap: wrap;
  margin-top: 40px;
}

.ourService__item {
  @include flex;
  margin: auto;
  justify-content: center;
  align-items: center;
  transition: 0.3s ease;
  width: 280px;
  padding-bottom: 30px;
  margin-top: 30px;
  &:hover {
    transition: 0.3s ease;
    box-shadow: 0px 10px 15px 0px rgba(0,0,0, 0.1);
  }
  &:hover .ourService__img-block {    
    border-top: 1px solid #f2f2f2;
    border-bottom-color: #fff;
  }
}

.ourService__img-block {
  @include flex;
  padding-top: 20px;
  padding-bottom: 20px;
  justify-content: center;
  align-items: center;
  border: 1px solid #f2f2f2;
  width: 280px;
  height: 282px;
}

.ourService__txt {
  font-family: $font-1;
  font-size: 20px;
  text-align: center;
  padding: 0;
  margin: 0;
  margin-top: 30px;
}

.ourService__price {
  font-family: $font-2;
  font-size: 24px;
  margin-top: 10px;
}

.ourService__link {
  @include btn;
  width: 240px;
  font-size: 16px;
  font-family: $font-1;
  border-width: 2px;
  margin-top: 10px;
  &:hover {
    color: $color-1;
  }
}

.ourService__link-size {
  display: block;
  margin: auto;
  font-family: $font-1;
  color: $color-2;
  font-size: 18px;
  text-align: center;
  margin-top: 20px;
}


@media #{$tablet} {
  .ourService__wrap {
    width: $max-tablet;
    margin: auto;
    margin-top: 30px;
  }
  
  .ourService__ttl {
    font-size: $h2-t;
    text-transform: uppercase;
  }
}

