.video__wrap {
  padding: 20px;
}

.video__ttl {
  @include line-p;
}

.video__items {
  @include nl;
  @include flex-row;
  flex-wrap: wrap;
  margin-top: 20px;
  justify-content: center;
}

.video__item {
  height: 300px;
  width: 280px;
  background: $color-1;
  margin-bottom: 20px;  
  margin-right: 10px;
}


@media #{$tablet} {
  .video__wrap {
    width: $max-tablet;
    margin: auto;
    margin-top: 20px;
  }
  
  .video__ttl {
    font-size: $h2-t;
    text-transform: uppercase;
  }
  
  .video__items {
    margin-top: 35px;    
  }

  .video__item {
    width: 352px;
    margin-right: 20px;
    &:nth-of-type(2n) {
      margin-right: 0;
    }
  }
}