.delivery {
  background: #778796 url(%=static=%/content/delivery-price.jpg)30%;
  background-size: cover;
  background-repeat: no-repeat;  
  margin-top: 30px;
}

.delivery__wrap {
  @include flex;
  margin-left: auto;
  padding: 20px;
  width: 170px;
  
}

.delivery__ttl {
  font-size: 24px;
  font-family: $font-2-b;
  text-transform: uppercase;
  text-align: center;
  color: $color-2;
  text-shadow: 1px 1px 1px rgba(0,0,0, 0.9);
  margin-bottom: 0;
}

.delivery__txt {
  font-family: $font-1-l-italic;
  color: #fff;
  font-size: 18px;
  text-shadow: 1px 1px 1px rgba(0,0,0, 1);    
}

.delivery__price {
  font-family: $font-2;
  font-size: 20px;
  color: $color-2;
  font-weight: normal;
}


@media #{$tablet} {
  .delivery__wrap {
    margin-top: 40px;
    width: 500px;
  }
  
  .delivery__ttl {
    font-size: $h1-t;    
  }
  
  .delivery__txt {
    font-size: 22px;
    text-align: right;
    padding-bottom: 20px;
  }
}


@media #{$desktop} {
  .delivery__wrap {
    width: $max-desktop;
    margin-top: 65px;
    margin-right: 380px;
    padding-bottom: 70px;
  }
  
  .delivery__ttl {
    padding-top: 55px;
    font-size: $h1-d;
    text-align: right;
    margin-bottom: 0;
  }
  
  .delivery__txt {
    width: 550px;
    margin-left: auto;
    font-size: 24px;
    line-height: 36px;
    margin-top: 3px;    
  }
  
  .delivery__price {
    font-size: 28px;
    font-family: $font-2;
    font-weight: bold;
  }
}