.printL {
  background: $color-4;
}
.printL__wrap {
  padding: 20px;  
}

.printL__img-block {
  @include flex;
  width: auto;
  height: auto;  
}

.printL__img {
  width: 100%;
  height: 100%;
}

.printL__ttl {
  font-family: $font-2-b;
  font-size: $h2-m;
  color: $color-1;
  text-align: center;  
  margin-top: 0px;   
}

.printL__pr-value {
  font-size: 18px;
  color: $color-3;
  font-family: $font-1;
}

.printL__price {
  @include flex-row();
  align-items: center;  
  margin: auto;
  width: 280px;
}

.printL__coin {  
  @include bg($coins);
  margin-right: 25px;
}

.printL__link {
  @include btn;
  margin-top: 20px;  
}


@media #{$tablet} {
  .printL__wrap {
    @include flex-row;    
    width: $max-tablet;
    align-items: center;
    margin: auto;
  }
  .printL__img-block {
    width: 50%;
  }
  .printL__info {
    @include flex;    
    width: 50%;
  }
  
  .printL__ttl {
    text-transform: uppercase;
    text-align: right;
    font-size: $h2-t;
  }
  
  .printL__link {
    margin-left: auto;
    width: 320px;
    margin-top: 50px;
  }
  
}


@media #{$desktop} {
  .printL__wrap {
    width: $max-desktop;
    padding: 30px;
    padding-bottom: 0;
    align-items: flex-start;
  }
  
  .printL__img-block {
    margin-left: 49px;
    height: 432px;
    overflow: hidden;    
  }
  
  .printL__img {
    height: 469px;
    width: 510px;
  }
  
  .printL__info {
    width: auto;
    margin-left: 20px;  
    margin-top: 65px;
  }
  
  .printL__ttl {
    font-size: $h2-d;
    letter-spacing: -0.5px;
    line-height: 54px;
    margin: 0;
  }
  
  .printL__price {
    width: auto;
  }
  
  .printL__coin {
    margin-left: 75px;
  }
  
  .printL__pr-value {
    font-size: 26px;
    line-height: 39px;
    margin-top: 10px;
    text-align: right;
  }
  
  .printL__link {
    width: 365px;
    margin-right: 2px;
    margin-top: 28px;
  }
}