.whyP__wrap {
  padding: 20px;
}

.whyP__ttl {  
  font-family: $font-2;
  font-size: $h2-m;    
  @include line-p;
}

.whyP__slider-wrap {
  @include flex-row;
  justify-content: center;
  align-items: center;
}

.whyP__items {
  @include nl;
  @include flex-row;
  flex-wrap: wrap;
  margin: auto;
  margin-top: 40px;  
  width: 240px;
}

.whyP__item {
  @include flex;
  justify-content: center;
  align-items: center;
}

.whyP__txt {
  font-family: $font-1-l;
  font-size: 22px;
  line-height: 34px;
  text-align: center;
}

.whyP__prev {
  @include bg($arrow-prev);
}

.whyP__next {
  @include bg($arrow-next);
}

.whyP__img--item1 {
  @include bg($pay);
}

.whyP__img--item2 {
  @include bg($pre-time);
}

.whyP__img--item3 {
  @include bg($all-sizes); 
}

.whyP__img--item4 {
  @include bg($XXL);
}

.whyP__img--item5 {
  @include bg($no-require);
}

.whyP__img--item6 {
  @include bg($washer);
}


@media #{$tablet} {
  .whyP__wrap {
    width: $max-tablet;
    margin: auto;
    margin-top: 30px;
  }
  
  .whyP__ttl {
    font-size: $h2-t;
    text-transform: uppercase;
  }
  
  .whyP__items {
    width: 768px;
    margin-left: auto;
    justify-content: space-between;
  }
  
  .whyP__item {
    width: 33.3%;  
    margin-top: 30px;
  }
  
  .whyP__prev,
  .whyP__next {
    display: none;
  }
}
