.why-s__wrap {
  padding: 20px;
  padding-bottom: 0;
}

.why-s__ttl-block {
  @include flex-row;
  justify-content: center;
  align-items: center;
}

.why-s__line {
  @include line;
  width: 10px;  
}

.why-s__ttl {
  @include nl;
  color: $color-1;
  font-size: $h2-m;
  font-family: $font-2;    
  margin-right: 15px;
  margin-left: 15px;
}


.why-s__items {
  @include nl;
  @include flex-row;
  flex-wrap: wrap;
  margin-top: 20px;
}

.why-s__item {
  @include flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-bottom: 32px;
}

.why-s__item-txt {
  font-family: $font-1-l;
  color: $color-1;
  font-size: 18px;
  text-align: center;
  margin-top: 20px;
}

.why-s__item-img { 
  @include flex;
}

.why-s__item-img--like {  
  @include bg($like);
}

.why-s__item-img--price {  
  @include bg($price);
}

.why-s__item-img--car {
  @include bg($car);
}


@media #{$tablet} {
  .why-s__wrap {
    width: $max-tablet;
    margin: auto;
    margin-top: 40px; 
  }
  .why-s__ttl {
    font-size: $h2-t;
    text-transform: uppercase;    
  }
  
  .why-s__line {
    width: 40px;
  }
  
  .why-s__items {
    flex-wrap: nowrap;
    justify-content: center;
    margin-top: 30px;
  }
  
  .why-s__item {
    flex-grow: 1;
    align-items: center;
    justify-content: flex-start;
  }

}


@media #{$desktop} {
  .why-s__wrap {
    width: $max-desktop;
    margin-top: 50px;
  }
  
  .why-s__ttl {
    font-size: $h2-d;
  }
  
  .why-s__line {
    width: 70px;
    margin: 0;    
  }
  
  .why-s__items {
    margin-top: 55px;
    padding-left: 9px;
  }
  
  .why-s__item-txt {
    font-size: 22px;
    line-height: 34px;
  }
}
