.discount {
  width: 100%;
}

.discount__wrap {
  padding: 20px;
}

.discount__info {
  display: block;
  margin: auto;
  font-family: $font-2;
  font-size: 24px;  
  text-align: center;
  color: $color-1;
}

.discount__percent {
  margin: auto;
  display: block;
  font-family: $font-2;
  text-transform: uppercase;
  color: #fff;
  font-size: 18px;
  text-align: center;
  padding: 20px;
  background: url(%=static=%/content/discount-txt-bg.png)no-repeat 50%;
  background-size: contain;
  margin-top: 10px;
  max-width: 260px;
}

.discount__link {
  @include btn;
  margin: auto;  
  font-size: 16px;
  padding-right: 20px;
  padding-left: 20px;
  margin-top: 5px;
  width: 200px;
}


@media #{$tablet} {
  .discount__wrap {       
    @include flex-row;
    flex-wrap: wrap;
    width: $max-tablet;   
    padding: 0;
    margin: auto;
    margin-bottom: 30px;
    margin-top: 30px;
  }
  
  .discount__info {
    font-size: $h2-t;
    text-transform: uppercase;
  }
  
  .discount__percent {
    font-size: $h2-t;
    margin-top: 0px;
  }
  
  .discount__link {    
    margin-top: 20px;
  }
}
