.print-shirts {
  background: #005564 url(%=static=%/content/print-shirts.jpg)no-repeat 35%;
  background-size: cover;
}

.print-shirts__wrap {
  padding: 20px;
  padding-bottom: 50px;
  width: 280px;
  margin-left: auto;
}

.print-shirts__ttl {
  font-size: $h2-m;
  text-align: right;
  font-family: $font-2-b;
  font-weight: normal;
  margin-top: 80px;
  color: #fff;
  text-shadow: 1px 1px 2px rgba(0,0,0, 0.9);
  margin-bottom: 0;
}

.print-shirts__info {
  @include flex-row;  
  align-items: center;
  justify-content: flex-end;
}

.print-shirts__medal {
  @include bg($medal);
}

.print-shirts__txt {
  font-family: $font-1;
  font-size: 18px;
  text-align: right;
  color: #fff;
  text-shadow: 1px 1px 1px rgba(0,0,0, 1);
}

.print-shirts__link {
  margin-top: 20px;
  @include btn;
  text-shadow: 1px 1px 2px rgba(0,0,0, 1);
  transition: 0.2s ease;  
}


@media #{$tablet} {
  .print-shirts__wrap {
    width: $max-tablet;
    margin: auto;
  }
  
  .print-shirts__link {
    width: 280px;
    margin-left: auto;
  }
}

@media #{$desktop} {
  .print-shirts {
    background-position: 65%;
  }
  .print-shirts__wrap {
    width: $max-desktop;
    padding-top: 35px;    
    padding-bottom: 135px;
  }
  
  .print-shirts__ttl {
    font-size: $h2-d;
    text-transform: uppercase;
    text-shadow: none;    
    line-height: 54px;
    padding-right: 165px;    
  }
  
  .print-shirts__txt {
    font-size: 26px;
    line-height: 39px;
    text-shadow: none;
    margin-top: 7px;    
    padding-right: 170px;    
  }
  
  .print-shirts__medal {
    margin-top: -10px;
    margin-right: 20px;
  }
  
  .print-shirts__link {
    width: 370px;
    margin-top: 5px;
    text-shadow: none;
    padding-top: 12px;
    padding-bottom: 12px;
    margin-right: 165px;
  }
}
