.print-info__wrap {
  padding: 20px;
}

.print-info__ttl {
  font-size: $h4-m;
  color: $color-1;
  font-family: $font-1-m;
  margin: 0;
}

.print-info__txt {
  font-family: $font-1-l;
  color: $color-1;
  font-size: 15px;
}

.print-info__h5 {
  font-family: $font-1-m;
  color: $color-1;
  font-size: 15px;
  margin: 0;
}

.print-info__items {
  @include nl;
  padding-left: 10px;
}

.print-info__item {
  @include flex-row;  
  font-size: 16px;
  font-family: $font-1-l;
  color: $color-1;
}

.print-info__item-line {
  height: 2px;
  width: 6px;
  background: $color-2;
  margin-top: 9px;
  margin-right: 10px;
}

.print-info__txt5 b {
  display: inline-block;
}

.print-info__print-ttl {
  font-family: $font-2;
  color: $color-1;
  font-weight: normal;
}

.print-info__print-txt {
  display: block;
  text-align: center;
  margin: auto;
}

.print-info__print-txt-d {
  display: block;
  margin: auto;
  font-size: 20px;
  background: url(%=static=%/content/txt-bg.png)no-repeat 50%;
  background-size: contain;
  padding: 15px;
  color: #fff;
  text-align: center;
  max-width: 280px;
}

.print-info__print-link {
  @include btn;
  width: 270px;
  margin: auto;
}


@media #{$tablet} {
  .print-info__wrap {
    width: $max-tablet;
    margin: auto;
  }
  .print-info__print-ttl {
    font-size: 30px;
  }
  
  .print-info__print-txt {
    display: block;
    font-size: $h2-t;
    text-transform: uppercase;
  }
  
  .print-info__print-txt-h {
    display: inline-block;
    font-size: $h2-t;    
    margin-left: 70px;
  }
  
  .print-info__print-txt-d {
    background: url(%=static=%/content/print-info-txt-bg.png)no-repeat 50%;
    background-size: contain;
    display: inline-block;
    font-size: $h3-t;
    max-width: none;
    text-transform: uppercase;
    padding: 25px;
  }
    
}

