.text__wrap {
  padding: 20px;
  padding-top: 0;
}

.text__ttl {
  font-size: $h3-m;
  font-family: $font-1-m;  
  color: $color-7;
}

.text__txt-ttl {
  font-size: 18px;
  font-family: $font-1-l;
  color: $color-7;
}

.text__lists {
  @include nl;
  padding-left: 10px;
}

.text__list {
  @include flex-row;
}

.text__list-line {  
  display: inline-block;
  height: 2px;
  width: 8px;
  background: $color-2; 
  margin-right: 10px;
  margin-top: 10px;
}

.text__list-txt {
  font-size: 15px;
  font-family: $font-1;
  color: $color-1;  
}

.text__txt {
  font-size: 15px;
  font-family: $font-1;
  color: $color-1;
}

.text__h5 {
  font-family: $font-1-m;
  font-size: 19px;
  color: $color-7;
}

@media #{$tablet} {
  .text__wrap {
    width: $max-tablet;
    margin: auto;   
  }
  
  .text__ttl {
    font-size: $h4-t;
    text-transform: uppercase;
  }
  
  .text__h5 {
    margin: 0;
    font-size: $h5-d;
  }
  
  .text__list-txt {
    font-size: 17px;
  }
  
  .text__txt {
    font-size: 17px;
  }
}


@media #{$desktop} {
  .text__wrap {
    width: $max-desktop;
    margin-top: 45px;
    padding-left: 40px;
  }
  
  .text__ttl {
    letter-spacing: -0.5px;
    margin-bottom: 8px;
  }
  
  .text__txt-ttl {
    margin-top: 0px;
    margin-bottom: 5px;
  }
  
  .text__lists {
    padding-left: 5px;
  }
  
  .text__list-txt {
    margin-left: 0px;
    margin-bottom: 7px;
  }
  
  .text__txt {    
    margin-bottom: 10px;
    line-height: 26px;
    letter-spacing: -0.2px;
    margin: 0;
    margin-bottom: 8px;
  }
  
  .text__txt0 {     
    margin-top: -5px;    
  }
  
  .text__txt1 {
    margin: 0;
  }
  
  .text__txt3 {
    margin-top: 5px;    
  } 

  .text__txt4 {   
    margin-bottom: 4px;
  } 
  
  .text__txt5 {
    margin-bottom: 3px;
  }
  
  .text__txt6 {
    margin-top: -5px;
    margin-bottom: 2px;
  }
  
  .text__txt9 {
    margin-top: -5px;
  }
  
  .text__txt10 {
    margin-bottom: 1px;
  }
  
  .text__txt11 {
    margin-top: -5px;
  }
  
  .text__txt13 {
    margin-top: -5px;
  }

  
  .text__h5 {
    font-weight: normal;    
    margin-top: 5px;
    margin-bottom: 5px;
  }
}