.footer {
  background: $color-7;
}

.footer__wrap {
  padding: 20px;
}

.footer__top-items {
  @include nl;  
  
}

.footer__top-items {  
  @include flex-row;
  flex-wrap: wrap;
  justify-content: center;
  
}

.footer__print-on-items,
.footer__print-items,
.footer__info-items {
  @include nl;  
  width: 200px;    
}

.footer__print-on-link,
.footer__print-link,
.footer__info-link {
  display: block;
  font-family: $font-1-l;
  font-size: 16px;
  text-decoration: none;
  color: #fff;
  padding: 10px;      
  &:hover {
    text-decoration: underline;
  }
}

.footer__top-item--phones {
  width: 100%;
  @include flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.footer__phone {
  font-family: $font-2;
  font-size: 18px;
  color: #fff;
  width: 200px;
  margin-bottom: 15px;
  text-decoration: none;
  &:before {
    content: '';
    display: inline-block;
    @include bg($phone);
    margin-right: 15px;
  }
}

.footer__phone-link {
  font-family: $font-1;
  font-size: 18px;
  color: $color-2;
}

.footer__bot-items {
  @include nl;
  @include flex;
}

.footer__bot-item {
  width: 300px;
  margin: auto;
}

.footer__bot-item--info {    
  margin-top: 20px;  
}

.footer__menu {
  @include nl;
  @include flex-row; 
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 20px;
  
}

.footer__menu-link {
  font-family: $font-1;
  font-size: 16px;  
  color: #fff;
  text-decoration: none;  
  padding-top: 10px;
  padding-bottom: 10px;
  margin-right: 13px; 
  transition: 0.3s ease;
  &:hover {
    transition: 0.3s ease;
    color: $color-3;
  }
  &:active {
    opacity: 0.5;
  }
}

.footer__menu-link--active {
  color: $color-2;
  &:hover {
    transition: 0.3s ease;
    color: $color-2;
  }  
}

.footer__bot-txt {
  display: block;
  font-family: $font-1-l;
  font-size: 14px;
  color: #fff;  
}


@media #{$tablet} {
  .footer__wrap {
    width: $max-tablet;
    margin: auto;
  }
  
  .footer__top-items {
    justify-content: space-between;
  }
  
  .footer__print-on-items,
  .footer__print-items,
  .footer__info-items {
    width: auto;
  }
  
  .footer__print-on-link,
  .footer__print-link,
  .footer__info-link {
    padding: 0;
    padding-bottom: 5px;
  }
  
  .footer__top-item--phones {
    width: auto;
  }
  
  .footer__top-item--phones {
    margin: 0;
    justify-content: flex-start;
  }

  
  .footer__phone {
    margin-bottom: 5px;
  }
  
  .footer__bot-items {
    flex-direction: row;
    justify-content: space-between;   
    margin-top: 15px;
  }
  
  .footer__bot-item {
    width: auto;  
    margin: 0;
  }
  
}


@media #{$desktop} {
  .footer__wrap {    
    width: $max-desktop;
    padding-top: 35px;
  }
  
  .footer__phone {
    font-size: 18px;
    width: auto;
  }
  
  .footer__phone-link {
    margin-left: 20px;
    font-size: 18px;
    letter-spacing: 2px;
    margin-top: -5px;
  }
  
  .footer__bot-items {
    justify-content: flex-start;
    margin-top: 10px;
  }
  
  .footer__bot-txt {
    font-size: 16px;    
  }
  
  .footer__bot-item--menu {
    margin-left: 115px;
  }
  
  .footer__bot-txt {
    display: block;
    margin-bottom: 8px;
  }
  
  .footer__menu {
    flex-wrap: wrap;
    margin-top: 30px;
  }
  
  .footer__menu-link {
    margin-right: 40px;
  }
}



