.order-p__wrap {
  padding: 20px;
  padding-top: 0px;
  width: auto;
  height: auto;
  margin-top: 30px;
}

.order-p__ttl {
  @include nl;
  font-family: $font-2;
  font-size: $h2-m;
  color: $color-1;
  text-align: center;
}

.order-p__info {
  display: block;
  font-family: $font-2;
  font-size: 20px;
  color: #fff;
  background: url(%=static=%content/txt-bg.png)no-repeat 50%;
  background-size: contain;
  padding: 10px;
  padding-left: 20px;
  padding-right: 20px;
  text-align: center;
  margin-top: 15px;
}

.order-p__forms {
  @include flex-row;
  flex-wrap: wrap;
  margin-top: 20px;
}

.order-p__input {
  outline: none;
}

.order-p__name {
  border: 0;
  background: $color-6;
  width: 100%;
  padding: 15px;
  border-radius: 6px;
  font-family: $font-1-l;
  font-size: 16px;
  border: 1px solid $color-4;
  transition: 0.3s ease;
  &:hover {
    border-color: $color-2;
  }
  &:focus {
    border-color: $color-2;
  }
}

.order-p__number {
  padding: 15px;
  color: $color-1;
  width: 100%;
  font-size: 16px;
  font-family: $font-1-l;
  border-radius: 6px;
  border: 1px solid $color-4;
  transition: 0.3s ease;
  margin-top: 20px;
  &:hover {
    border-color: $color-2;
  }
  &:focus {
    border-color: $color-2;
  }
}

.order-p__sub {
  @include btn;
  width: 100%;
  border-width: 2px;
  background: #fff;
  font-size: 16px;
  padding: 14px;
  margin-top: 20px;
}

@media #{$tablet} {
  .order-p__wrap {
    width: $max-tablet;
    margin: auto;
    margin-top: 30px;
  }
  
  .order-p__ttl {
    width: 70%;
    margin: auto;
    font-size: $h2-t;
    text-transform: uppercase;
  }
  
  .order-p__info {
    font-size: $h2-t;
    text-transform: uppercase;
  }
  
  .order-p__forms {    
    margin-top: 30px;
    flex-wrap: nowrap;    
  }
  
  .order-p__input {
    margin: 0;
  }
  
  .order-p__name,
  .order-p__number {
    margin-right: 20px;
  }
}


@media #{$desktop} {
  .order-p__wrap {
    width: $max-desktop;
    margin-top: 60px;
  }
  
  .order-p__ttl {
    font-size: $h3-d;
    width: auto;
  }
  
  .order-p__info {
    margin-top: 11px;
    font-size: $h3-d;
  }
  
  .order-p__forms {
    padding-left: 200px;
  }
  
  .order-p__input {
    padding: 10px;
    width: 210px;
    margin-right: 30px;
  }
  
  .order-p__number {
    width: 230px;
  }
  
  .order-p__sub {
    width: 250px;
  }
}



