.and-do__wrap {
  padding: 20px;
}

.and-do__ttl {
  display: block;
  font-family: $font-1-l;
  font-size: 18px;
  text-align: center;
}

.and-do__items {
  @include nl;
  @include flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.and-do__item {  
  @include flex-row;
  align-items: center;  
  width: 280px;
  margin-bottom: 10px;  
}

.and-do__nbr {
  @include flex;
  justify-content: center;
  align-items: center;
  background: $color-2;
  color: #fff;
  border-radius: 50%;
  padding: 8px;
  height: 8px;
  margin-right: 15px;
  line-height: 5px;
}

.and-do__link {
  font-family: $font-1;
  font-size: 18px;
  color: $color-2;
  &:hover {
    color: $color-3;
  }
  &:active {
    opacity: 0.5;
  }
}


@media #{$tablet} {
  .and-do__wrap {
    width: $max-tablet;
    margin: auto;
    margin-top: 30px;
    @include flex-row;    
  } 
  
  .and-do__items {
    margin: 0;
    flex-direction: row;
    justify-content: space-between;  
    margin-left: 80px;
    flex-grow: 1;
  }
  
  .and-do__item {
    width: auto;
  }
}


@media #{$desktop} {
  .and-do__wrap {
    width: $max-desktop;
    margin-top: 50px;
    margin-bottom: 25px;
  }
  
  .and-do__ttl {
    margin-left: 110px;
    margin-right: 90px;
  }
  
  .and-do__items {
    flex-grow: 0;
  }
  
  .and-do__item:not(:nth-of-type(2n)) {
    margin-right: 190px;
  }
}
