.clients__wrap {
  padding: 20px;
  margin-top: 20px;
}

.clients__ttl-block {
  @include flex-row;
  justify-content: center;
  align-items: center;
}

.clients__line {
  @include line;
  width: 10px;
}

.clients__ttl {
  font-family: $font-2;
  font-size: $h2-m;
  margin: 0;
  margin-right: 15px;
  margin-left: 15px;
  color: $color-1;
}

.clients__slider-wrap {
  @include flex-row;
  justify-content: center;
  align-items: center;
  margin-top: 40px;
}

.clients__prev {
  @include bg($arrow-prev);
  margin-right: 10px;
}

.clients__next {
  @include bg($arrow-next);
  margin-left: 10px;  
}

.clients__slider-items {
  @include nl;
  width: 80%;   
  @include flex;
  align-items: center;
  justify-content: center;  
}

.clients__item {
  @include flex;
  align-items: center;
  justify-content: center;
  min-height: 100px;
}

.clients__img {
  height: 100%; 
}


@media #{$tablet} {
  .clients__wrap {
    width: $max-tablet;
    margin: auto;
    margin-top: 40px;    
  }
  
  .clients__ttl {
    font-size: $h2-t;    
  }
  
  .clients__line {
    width: 35px;
  }
  
  .clients__slider-items {
    margin-left: 20px;
    margin-right: 0px;
  }
  
  .clients__item {
    margin-right: 20px;    
  }
}


@media #{$desktop} {
  .clients__wrap {
    width: $max-desktop;
    margin-top: 50px;
  }
  
  .clients__ttl {
    font-size: $h2-d;
  }
  
  .clients__line {
    width: 70px;
  }
  
  .clients__slider-items {
    width: 1000px;
    margin-right: 50px;
    margin-left: 50px;
  }
  
  
}
