.price-s__wrap {
  padding: 20px;
  padding-top: 0;
}

.price-s__ttl-block {
  @include flex-row();
  align-items: center;
  justify-content: center;
}

.price-s__line {
  @include  line;
  width: 15px;
  margin-left: 10px;
  margin-right: 10px;
}

.price-s__ttl {
  @include nl;
  font-size: $h2-m;
  color: $color-1;
  text-align: center;  
}

.price-s__txt {
  @include nl;
  font-family: $font-1-l-italic;
  text-align: center;
  color: $color-1;
  margin-top: 10px;  
}

.price-s__edit-wrap {
  @include flex-row;
  margin: auto;
  justify-content: center;
  margin-top: 30px; 
}
.price-s__edit-prev {
  content: '';
  display: block;
  @include bg($arrow-prev);
  align-self: center;
  margin-right: 20px;
}

.price-s__edit-next {
  content: '';
  display: block;
  @include bg($arrow-next);
  align-self: center;
  margin-left: 20px;
}




.price-s__edit {
  @include flex;
  margin-right: -1px;
  width: 100px;
}

.price-s__items {
  @include nl;
  width: 200px;  
}

.price-s__item {
  width: 200px;  
}

.price-s__th-ttl {
  color: #fff;
  background: $color-2;
  padding: 10px;   
  text-align: center;
  border: 1px solid #f29a00;
  font-family: $font-1;
  font-size: 16px;
  font-weight: normal;
  width: 200px;
}

.price-s__td-info {
  color: $color-1;
  text-align: center;
  padding: 10px;
  border: 1px solid $color-5;
  font-family: $font-1-l;
  font-size: 16px;
}

.price-s__open-all {
  display: block;
  text-align: center;
  margin-top: 12px;
  font-family: $font-1;
  color: $color-2;
  font-size: 16px;
}

@media #{$tablet} {
  .price-s__wrap {
    width: $max-tablet;
    margin: auto;
    margin-top: 40px;
  }
  
  .price-s__ttl {
    font-size: $h2-t;
    text-transform: uppercase;
  }
  
  .price-s__line {
    width: 35px;
  }
  
  .price-s__txt {
    font-size: 20px;
  }
  
  .price-s__edit {
    width: 150px;
  }
  
  .price-s__items {
    @include flex-row;    
    width: auto;
    margin-left: 5;
    border-left: 1px solid rgba(0,0,0, 0);
  }
  
  .price-s__item {
    width: 150px;
    margin-left: -1px;
  }
  
  .price-s__open-all {
    margin-top: 30px;
  }
  
  .price-s__edit-prev, 
  .price-s__edit-next {
    display: none;
  } 

}


@media #{$desktop} {
  .price-s__wrap {
    width: $max-desktop;    
    margin-top: 30px;
  }
  
  .price-s__ttl {
    font-size: $h2-d;
  }
  
  .price-s__line {
    width: 70px;
  }
  
  .price-s__txt {
    font-size: 24px;
    line-height: 35px;
    margin-top: 2px;
  }
  
  .price-s__edit-wrap {
    margin-top: 52px;
    margin-left: 6px;
  }
  
  .price-s__edit {
    width: 161px;
  }
  
  .price-s__item {
    width: 161px;
  }
  
  .price-s__th-ttl {
    padding-top: 13px;
    padding-bottom: 13px;
  }
  
  .price-s__td-info {
    padding-top: 15px;
    padding-bottom: 15px; 
  }
}



