.header {
  background: $color-1;
  border-bottom: 1px solid $color-3;  
  box-shadow: 0px 1px 0px 0px #000;
}

.header__wrap {  
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 20px;
}

.header__items {
  @include nl();
  display: none;
  flex-wrap: wrap;
  justify-content: center;
  padding-bottom: 20px;
}

.header__menu-open {  
  @include flex;  
  transition: 0.3s ease;
  border-top: 1px solid $color-3;  
  box-shadow: 0px -1px 0px 0px #000;
}

.header__item {
  @include flex-row();
  align-items: center;
  justify-content: center;
}

.header__logo-block {
  @include flex-row;
  padding-bottom: 10px;  
}

.header__logo {
  display: inline-block;
  vertical-align: middle;
  margin-right: 16px;
  height: 50px;
}

.header__logo-txt {
  @include nl;
  display: inline-block;
  vertical-align: middle;
  font-size: 14px;
  font-family: $font-2;
  color: #fff;  
}

.header__menu-btn {  
  width: 45px;
  height: 45px;
  margin-left: auto;
  padding-top: 10px;
  cursor: pointer;
}

.header__menu-btn-line,
.header__menu-btn-line2,
.header__menu-btn-line3 {
  display: block;
  height: 3px;
  width: 45px;
  background: #fff;     
}

.header__menu-btn-line2 {  
  margin-top: 9px;  
}

.header__menu-btn-line3 {  
  margin-top: 9px;  
}

.header__item--nav {
  @include flex;
  margin: auto;
  justify-content: center;
  width: 100%;
}

.header__contacts {
  margin-top: 15px;
  order: 2;
}

.header__cont-txt {
  font-size: 16px;
  font-family: $font-1-l;
  color: #fff;  
  margin-top: 5px;
}

.header__adress {
  &:before {
    content: '';
    display: inline-block;
    margin-right: 10px;
    @include bg($marker);
  }
}

.header__mail {
  &:before {
    content: '';
    display: inline-block;
    margin-right: 10px;
    @include bg($mail);
  }
}

.header__menu {
  @include nl;
  order: 1;
  width: 100%;
}

.header__menu-link {
  @include nl;  
  display: block;
  color: #fff;
  font-size: 16px;  
  font-family: $font-1;
  padding-bottom: 2px;  
  text-align: center;
  padding-top: 13px;
  padding-bottom: 13px;
  border-bottom: 1px solid $color-3;
  box-shadow: 0px 1px 0px 0px #000;
  transition: 0.3s ease;
  &:hover {
    transition: 0.3s ease;
    color: $color-3;
  }
  &:active {
    transition: 0.3s ease;
    opacity: 0.5;
  }
}

.header__menu-link--active {
  color: $color-2;  
  &:hover {
    color: $color-2;
  }
}

.header__phones {  
  margin-top: 15px;
}

.header__nbr {
  display: block;
  font-family: $font-2;
  font-size: 20px;
  color: #fff;
  padding-right: 25px;
  text-decoration: none;
  margin-top: 5px;
  &:before {
    content: '';
    display: inline-block;
    @include bg($phone);
    margin-right: 10px;
    margin-bottom: -2px;
  }
}

.header__phone-link {
  display: block;
  margin: auto;
  font-family: $font-1;
  color: $color-2;
  font-size: 21px;  
  padding-left: 20px;
  padding-top: 15px;
  transition: 0.3s ease;
  &:hover {
    transition: 0.3s ease;
    color: $color-3;
  }
  &:active {
    opacity: 0.5;
  }
}


@media #{$tablet} {
  .header__wrap {
    @include flex-row;    
    padding-bottom: 10px;
    padding-top: 20px;
    width: $max-tablet;
    margin: auto;
  }
  
  .header__logo-block {
    align-items: center;
    margin-right: 5px;
  }
  
  .header__logo {
    height: 60px;
    margin-right: 10px;    
  }
  
  .header__logo-txt {
    font-size: 14px;
  }
  
  .header__menu-btn {
    display: none;
  }
  
  .header__items {
    @include flex-row;
    padding: 0;
    flex-wrap: wrap;
    border: none;
    box-shadow: none;    
    flex-grow: 1;    
  }
  
  .header__item {    
    align-items: flex-start;    
  }
  
  .header__item--nav {
    border: none;
    width: auto;    
    justify-content: center;
    align-items: center;
    margin-top: 10px;
  }  
  
  .header__contacts {
    margin: 0;
    @include flex-row;
    align-items: center;
    justify-content: center;
  }
  
  .header__cont-txt {
    margin: 0;    
    font-size: 13px;
    margin-right: 10px;
    &:before {
      display: inline-block;
      margin-bottom: -2px;
    }
  }
  
  .header__menu {
    display: flex;
    flex-direction: row;
    order: 5;
    border: none;   
    margin-top: 0px;
    width: auto;
  }
  
  .header__menu-link {
    border: none;
    box-shadow: none;
    margin-right: 20px;
  }
  
  .header__item--phones {
    margin-left: auto;
  }
    
  .header__phones {
    margin: 0; 
    margin-top: 5px;
  }
  
  .header__nbr {
    padding: 0;
    font-size: 15px;
    margin: 0;    
  }
  
  .header__phone-link {
    font-size: 15px;
    padding-top: 0px;
  }
}


@media #{$desktop} {
  .header__wrap {
    width: $max-desktop;
    padding: 30px;
    padding-bottom: 0;
  }
  
  .header__logo-block {
    align-items: flex-start;
  }
  
  .header__logo {
    height: 76px;
    width: 76px;
    margin-left: 9px;
    margin-right: 20px;    
  }
  
  .header__logo-txt {
    font-size: 20px;
    line-height: 27px;
  }  
  
  .header__items {    
    margin-left: 128px;
    align-items: flex-start;
    justify-content: flex-start;
  }
  
  .header__item--nav {
    margin: 0;
    justify-content: flex-start;
    align-items: flex-start;
    margin-top: 5px;
  }
  
  .header__cont-txt {
    font-size: 16px;
    margin-right: 21px;
  }
  
  .header__menu {
    margin-top: 20px;
    width: 500px;
    margin-left: 37px;
    flex-wrap: wrap;
  }
  
  .header__menu-link {
    margin-right: 39px;
    padding-bottom: 30px;
    &:nth-of-type(5n) {
      margin-right: 0;
    } 
  }
  
  .header__menu-link--active {
    padding-bottom: 30px;
    border-bottom: 2px solid $color-2;
  }
  
  .header__phones {
    margin: 0;
  }
  
  .header__nbr {
    font-size: 20px;
    margin-bottom: 4px;
  }
  
  .header__phone-link {
    font-size: 21px;    
  }
  
  
  
  
  
  
  
  
  
  
  
  
  
  
}